.carousel-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
  
    &__image {
      max-width: 100%;
      max-height: 100%;
    }
  
    &__content {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 1rem;
      background-color: rgba(0, 0, 0, 0.7);
      color: #fff;
      width: 100%;
      box-sizing: border-box;
  
      & > * {
        margin: 0;
      }
    }
  
    &--active {
      opacity: 1;
    }
  
    &--prev,
    &--next {
      opacity: 0;
    }
  
    &--prev {
      transform: translateX(-100%);
    }
  
    &--next {
      transform: translateX(100%);
    }
  }

  .carousel__back {
    height: 300px;
  }