/* CarouselList.scss */

.carousel-container {
    position: relative;
    height: 300px;
    overflow: hidden;
  
    @media screen and (min-width: 768px) {
      height: auto;
    }
  }
  
  .carousel-items-container {
    display: flex;
    position: relative;
    height: 100%;
    width: 300%;
  
    @media screen and (min-width: 768px) {
      width: 900%;
    }
  }
  
  .carousel-prev-button,
  .carousel-next-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    background-color: transparent;
    color: #fff;
    font-size: 2rem;
  
    @media screen and (min-width: 768px) {
      font-size: 3rem;
    }
  }
  
  .carousel-prev-button {
    left: 0.5rem;
  }
  
  .carousel-next-button {
    right: 0.5rem;
  }
  
  .carousel-prev-button:focus,
  .carousel-next-button:focus {
    outline: none;
  }
  